.gradient-circle {
	background: conic-gradient(
		#55b7a4 0%,
		#4ca493 40%,
		#fff 40%,
		#fff 60%,
		#336d62 60%,
		#2a5b52 100%
	);
	height: 320px;
	width: 320px;
	z-index: -2;
	border-radius: 50%;
	position: absolute;
	top: -10px;
	left: -10px;
}

.pointer {
	background-color: #fff;
	border-radius: 50%;
	height: 20px;
	width: 20px;
	display: block;
}

.pointer-svgContainer {
	position: absolute;
	top: -40px;
	left: 140px;
	width: 20px;
	height: 190px;
	animation: rotate 7.5s linear forwards infinite;
	transform-origin: bottom center;
}

@keyframes rotate {
	from {
		transform: rotate(0deg);
	}

	to {
		transform: rotate(360deg);
	}
}

.svgContainer.grow {
	animation: grow 3s linear forwards;
}

@keyframes grow {
	from {
		transform: scale(1);
	}

	to {
		transform: scale(1.2);
	}
}

.svgContainer.shrink {
	animation: shrink 3s linear forwards;
}

@keyframes shrink {
	from {
		transform: scale(1.2);
	}

	to {
		transform: scale(1);
	}
}
